//	colors
$gn01:	#007F41;
$gn02:	#E6F5BD;
$gn03:	#003C18;
$bl01:	#260096;
$pk01:	#FCE2E2;
$pk02:	#FFF5FA;
$yl01:	#FFFAB8;
$yl02:	#FFF0BE;
$sk01:	#D9F8FF;
$rd01:	#F84263;
$or01:	#FB6736;
$gy01:	#4B4E53;

// unit
$gap: 20px;
$sp: 767px;

// font
$fontXS: 12px;
$fontS:	14px;
$fontM:	16px;
$fontL:	20px;
$fontXL: 24px;


@media (min-width: $sp + 1) {
	.content_inner {
		section {
			&.sec01 {
				.dacky {
					top: -11px;
					.txts {
						left: 322px;
						bottom: 50px;
						width: 638px;
					}
				}
				.badge {
					img {
						width: 240px;
						
					}
				}
				.btn_apply {
					margin-top: $gap * 2;
				}
			}
			&.sec02 {
				h2 {
					+ {
						div {
							margin: $gap / 2 $gap / 2 0;
						}
					}
				}
				> div {
					padding: $gap;
				}
				.flex {
/*
					.div_l {
						flex-basis: 75%;
						font-size: $fontXL;
					}
					.div_r {
						flex-basis: 20%;
						display: flex;
						align-items: center;
						position: relative;
						top: $gap * -2;
					}
*/
				}
			}
		}
	}
}

.youtube {
	background: $bl01;
	padding: $gap / 2 $gap / 2 $gap / 4;
	margin: $gap / 2;
	@media (min-width: $sp + 1) {
		padding: $gap / 2;
		margin: $gap;
	}
	> p {
		img {
			width: 100%;
			text-align: center;
		}
		margin: $gap / -4 $gap / 2 $gap / 2;
		@media (min-width: $sp + 1) {
			margin: $gap / 4 $gap * 8 $gap / 1.5;
		}
	}
	.ifr_cnt {
		position: relative;
		width: 100%;
		padding: 56.25% 0 0 0;
		margin-bottom: $gap / 8;
		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
}
